import React from 'react';
import Flickity from 'flickity';
import ItemSlider from './ItemSlider';

export default class TopHeader extends React.Component {
    constructor(props) {
        super(props);
    }

    componentDidMount() {
        var elem = document.querySelector('.insideSlider');
        this.flickity = new Flickity(elem, {
            cellAlign: 'left',
            contain: true,
            prevNextButtons: false,
            pageDots: false
        });
    }

    render() {
        return (
            <div className="topHeader">
                <div className="insideSlider">
                    <ItemSlider nombre={process.env.PUBLIC_URL + "/style.mp4"}/> 
                </div>
                <div className="overlay">{this.props.nombre}</div>
            </div>
        )
    } 
}