import React from 'react';
import p5 from 'p5';
import "p5/lib/addons/p5.dom";
import ml5 from 'ml5';

export default class LunaBlock extends React.Component {
    
    constructor(props){
        super(props);
        this.state = {
            loading: true,
            canvas: null,
            color: 'color_1'
        }
    }

    componentDidMount() {
        const modelUrl = process.env.PUBLIC_URL + "/luna.pict";
        let self = this;

        var scketch = function(p) {

            var color = p.color(255,0,0);
            var isTransfering = false;
            var modelReady = false;
            var pix2pix;
            var sW;
        
            p.setup = function() {
                p.createCanvas(256,256);
                p.background(0);
        
                pix2pix = ml5.pix2pix(process.env.PUBLIC_URL + "/luna.pict", function(){
                    modelReady = true;
                    self.setState({
                        loading: false
                    });
                    p.transfer();
                });
        
                sW = 30;
            }
        
            p.draw = function() {
                p.stroke(color);
                p.strokeWeight(sW);
                p.noFill();
            }
                
            p.mouseReleased = function() {
                if(modelReady && !isTransfering) p.transfer();
            }
        
            p.mouseDragged = function() {
                p.strokeWeight(sW);
                p.line(p.pmouseX, p.pmouseY, p.mouseX, p.mouseY);
            }
        
            p.changeColor = function(color_temp){
                color = p.color(color_temp[0],color_temp[1],color_temp[2]);
        
                if(color_temp[0] === '0' && color_temp[1] === '0' && color_temp[2] === '0') sW = 30;
                else if(color_temp[0] === '255') sW = 30;
                else if(color_temp[1] === '255') sW = 4;
                else if(color_temp[2] === '255') sW = 12;
            }
        
            p.transfer = function() {
                isTransfering = true;
        
                const canvas = p.select('canvas').elt;
        
                pix2pix.transfer(canvas, function(err, result){
                    if(err) console.log(err);
        
                    isTransfering = false;
                    let padre = document.getElementById('output');
                    if(padre) {
                        while(padre.firstChild) padre.removeChild(padre.firstChild);
                        p.createImg(result.src).class('resultado').parent('output')
                    };
                });
            }
        }

        var canvas_draw = new p5(scketch, 'canvas_container');

        let btns = document.getElementsByClassName('color');
        for(let i=0;i<btns.length;i++) {
            btns[i].addEventListener('click', (e) => {
                const color = e.target.getAttribute('data-color');
                const colorString = 'color_' + (i+1);
                self.setState({
                    color: colorString
                });
                canvas_draw.changeColor(color.split(','));
            })
        };

        this.setState({
            canvas: canvas_draw
        })
    }

    componentWillUnmount() {
        this.state.canvas.remove();
    }

    render() {
        let data;
        if(this.state.loading === true){
            data = <div className="loading">Cargando modelo...</div>
        } else {
            data = '';
        }
        return (
            <>
                {data}
                <div className={(this.state.loading === false) ? 'inside' : 'inside hidden'}>
                    
                    <div className="canvas" id="canvas_container">
                        <div className="paleta luna">
                            <div data-color="255,0,0" className={(this.state.color === 'color_1') ? 'color active' : 'color'}></div>
                            <div data-color="0,255,0" className={(this.state.color === 'color_2') ? 'color active' : 'color'}></div>
                            <div data-color="0,0,255" className={(this.state.color === 'color_3') ? 'color active' : 'color'}></div>
                            <div data-color="0,0,0" className={(this.state.color === 'color_4') ? 'color active' : 'color'}></div>
                        </div>
                    </div>
                    <div className="output" id="output"></div>
                </div>
                
            </>
        );
    }
}