import React from 'react';
import MontanaBlock from './MontanaBlock';
import LunaBlock from './LunaBlock';
import TopoBlock from './TopoBlock';

export default class Pix2pixComponent extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            block: ''
        }
    }

    componentDidMount() {
        let btns = document.getElementsByClassName('itemBlock');
        let self = this;
        for(var i=0;i<btns.length;i++) {
            btns[i].addEventListener('click', (e) => {
                const block = e.target.getAttribute('data-block');
                self.setState({
                    block: block
                })
            })
        };
    }

    render() {
        let block = <div className="loading">Selecciona uno de los tres modelos disponibles.</div>

        if(this.state.block === 'montana') block = <MontanaBlock />
        else if(this.state.block === 'luna') block = <LunaBlock />
        else if(this.state.block === 'topo') block = <TopoBlock />
        
        return(
            <div className="inside">
            <div className="info">
                <p>El siguiente ejercicio ha sido desarrollado usuando la popular librería <a href="https://github.com/affinelayer/pix2pix-tensorflow">pix2pix</a> para entrenar el modelo y <a href="">ml5.js</a> para su implementación en el navegador</p>
            </div>
            <div className="nav">
                <div className={(this.state.block === 'luna') ? 'itemBlock active' : 'itemBlock'} data-block="luna">Paisaje Lunar</div>
                <div className={(this.state.block === 'topo') ? 'itemBlock active' : 'itemBlock'} data-block="topo">Topografía</div>
                <div className={(this.state.block === 'montana') ? 'itemBlock active' : 'itemBlock'} data-block="montana">Montañas</div>
            </div>

            {block}
            
            </div>
        );
    }
}